function sliderDoctors() {
    var swiper = new Swiper('.doctors .swiper', {
        slidesPerView: '1',
        spaceBetween: 20,
        loop: false,
        navigation: {
            nextEl: '.doctors .swiper-button-next',
            prevEl: '.doctors .swiper-button-prev',
        },
        pagination: {
            el: ".doctors .swiper-pagination",
            clickable: true,
        },

    })
}

function sliderLicenses() {
    var swiper = new Swiper('.licenses .swiper', {
        slidesPerView: '4',
        spaceBetween: 20,
        loop: false,
        navigation: {
            nextEl: '.licenses .swiper-button-next',
            prevEl: '.licenses .swiper-button-prev',
        },
        pagination: {
            el: ".licenses .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            401: {
                slidesPerView: '2',
            },
            769: {
                slidesPerView: '3',
            },
            1201: {
                slidesPerView: '4',
            }
        }

    })
}

function sliderArticles() {
    var swiper = new Swiper('.articles .swiper', {
        slidesPerView: '4',
        spaceBetween: 20,
        loop: false,
        navigation: {
            nextEl: '.articles .swiper-button-next',
            prevEl: '.articles .swiper-button-prev',
        },
        pagination: {
            el: ".articles .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            577: {
                slidesPerView: '2',
            },
            993: {
                slidesPerView: '3',
            },
            1401: {
                slidesPerView: '4',
            }
        }
    })
}

$(document).ready(function() {

    sliderDoctors();
    sliderLicenses();
    sliderArticles();

    // Show - Hidden text
    $('.show').click(function () {
        $(this).parents('.reviews__item-text').addClass('reviews__item-text--active')
    })

    $('.hidden').click(function () {
        $(this).parents('.reviews__item-text').removeClass('reviews__item-text--active')
    })


})